import { Inject, Injectable, Optional } from '@angular/core';
import { ItemModel } from '@backbase/foundation-ang/core';

import { BehaviorSubject, combineLatest, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import {
  accessControlConfigDefaults,
  BB_STOP_CHECK_ACCESS_CONTROL_CONFIG_TOKEN,
} from '../config.providers';
import { currencyFallback } from '../models/data';
import { Property } from '@backbase/foundation-ang/web-sdk';
import { SchemeName } from '@backbase/data-ang/stop-checks';
import {
  AccessControlConfigProvider,
  toInteger,
} from '@backbase/payment-orders-ang';

export const defaultStopChecksCreatePreferences = {
  currencies: [currencyFallback],
  notificationDismissTime: 3,
  checksRangeNumber: 0,
  schemeName: 'BBAN',
  ...accessControlConfigDefaults,
};

@Injectable()
export class StopChecksPreferencesService {
  private readonly accessControlConfig: AccessControlConfigProvider = {
    ...defaultStopChecksCreatePreferences,
    ...(this.accessControlToken || {}),
  };

  private readonly resourceNameSubject$ = new BehaviorSubject<
    string | undefined
  >(undefined);
  private readonly businessFunctionSubject$ = new BehaviorSubject<
    string | undefined
  >(undefined);
  private readonly privilegeSubject$ = new BehaviorSubject<string | undefined>(
    undefined
  );
  private readonly notificationDismissTimeSubject$ = new BehaviorSubject<
    number | undefined
  >(undefined);
  private readonly currenciesSubject$ = new BehaviorSubject<
    string[] | undefined
  >(undefined);
  private readonly checksRangeNumberSubject$ = new BehaviorSubject<
    number | undefined
  >(undefined);
  private readonly schemeNameSubject$ = new BehaviorSubject<
    SchemeName | undefined
  >(undefined);

  private readonly schemeNameItemModel = this.getItemModelProperty(
    'schemeName',
    this.accessControlConfig.schemeName
  );

  private readonly resourceNameItemModel = this.getItemModelProperty(
    'resourceName',
    this.accessControlConfig.resourceName
  );

  private readonly businessFunctionItemModel = this.getItemModelProperty(
    'businessFunction',
    this.accessControlConfig.businessFunction
  );
  private readonly privilegeItemModel = this.getItemModelProperty(
    'privilege',
    this.accessControlConfig.privilege
  );
  private readonly notificationDismissTimeItemModel = this.getItemModelProperty(
    'notificationDismissTime',
    defaultStopChecksCreatePreferences.notificationDismissTime
  ).pipe(map(toInteger));

  private readonly currenciesItemModel = this.getItemModelProperty(
    'currencies',
    defaultStopChecksCreatePreferences.currencies[0]
  ).pipe(
    map((currencies: string) =>
      currencies.split(',').map((currency: string) => currency.trim())
    )
  );

  private readonly checksRangeNumberItemModel = this.getItemModelProperty(
    'checksRangeNumber',
    defaultStopChecksCreatePreferences.checksRangeNumber
  ).pipe(map(toInteger));

  readonly schemeName$ = this.getProperty<SchemeName>(
    this.schemeNameSubject$,
    this.schemeNameItemModel
  );
  readonly resourceName$ = this.getProperty<string>(
    this.resourceNameSubject$,
    this.resourceNameItemModel
  );
  readonly businessFunction$ = this.getProperty<string>(
    this.businessFunctionSubject$,
    this.businessFunctionItemModel
  );
  readonly privilege$ = this.getProperty<string>(
    this.privilegeSubject$,
    this.privilegeItemModel
  );
  readonly notificationDismissTime$ = this.getProperty<number>(
    this.notificationDismissTimeSubject$,
    this.notificationDismissTimeItemModel
  );
  readonly currencies$ = this.getProperty<string[]>(
    this.currenciesSubject$,
    this.currenciesItemModel
  );
  readonly checksRangeNumber$ = this.getProperty<number>(
    this.checksRangeNumberSubject$,
    this.checksRangeNumberItemModel,
    true
  );

  private getProperty<T>(
    input$: Observable<T | undefined>,
    itemModel: Observable<Property>,
    t?: any
  ): Observable<T> {
    return combineLatest([input$, itemModel]).pipe(
      map(([value, valueIM]) => (value === undefined ? valueIM : value) as T)
    );
  }

  private getItemModelProperty<T extends Property>(
    name: string,
    defaultVal: T
  ): Observable<T> {
    return this.itemModel?.property<T>(name, defaultVal) || of(defaultVal);
  }

  setResourceName(value: string) {
    this.resourceNameSubject$.next(value);
  }

  setBusinessFunction(value: string) {
    this.businessFunctionSubject$.next(value);
  }

  setPrivilege(value: string) {
    this.privilegeSubject$.next(value);
  }

  setNotificationDismissTime(value: number) {
    this.notificationDismissTimeSubject$.next(value);
  }

  setCurrencies(value: string[]) {
    this.currenciesSubject$.next(value);
  }

  setChecksRangeNumber(value: number) {
    this.checksRangeNumberSubject$.next(value);
  }

  constructor(
    @Optional() private readonly itemModel: ItemModel,
    @Optional()
    @Inject(BB_STOP_CHECK_ACCESS_CONTROL_CONFIG_TOKEN)
    private readonly accessControlToken: AccessControlConfigProvider
  ) {}
}
