import {
  ChangeDetectionStrategy,
  Component,
  OnDestroy,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { StopCheckPostResponse } from '@backbase/data-ang/stop-checks';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { pluck, take, takeUntil } from 'rxjs/operators';
import { StopChecksPreferencesService } from '../../services/stop-checks-preferences.service';

import { InitialState, StopCheckRequestInfo } from '../../models/types';
import { ApiHttpService } from '../../services/data.service';
import { StateService } from '../../store/state.service';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  selector: 'bb-review-container',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './review-container.component.html',
})
export class ReviewContainerComponent implements OnDestroy {
  private readonly gc$ = new Subject();
  private notificationDismissTimeout!: number;

  constructor(
    private readonly stateService: StateService,
    private readonly apiHttpService: ApiHttpService,
    private readonly stopChecksPreferencesService: StopChecksPreferencesService,
    private readonly notificationService: NotificationService
  ) {
    this.stopChecksPreferencesService.notificationDismissTime$
      .pipe(takeUntil(this.gc$))
      .subscribe((value: number) => (this.notificationDismissTimeout = value));
  }

  @ViewChild('successNotificationHeader')
  successNotificationHeader!: TemplateRef<any>;
  @ViewChild('successNotificationMessage')
  successNotificationMessage!: TemplateRef<any>;
  @ViewChild('failureNotificationHeader')
  failureNotificationHeader!: TemplateRef<any>;
  @ViewChild('failureNotificationMessage')
  failureNotificationMessage!: TemplateRef<any>;

  isStopRequestSubmitted$ = new BehaviorSubject<boolean>(false);
  requestInfo$: Observable<InitialState['requestInfo']> =
    this.stateService.store.pipe(pluck('requestInfo'));
  hasErrorOnSubmit$ = new BehaviorSubject<boolean>(false);

  private showNotification(
    success: boolean,
    notification: {
      header: string | TemplateRef<any>;
      message: string | TemplateRef<any>;
    }
  ) {
    this.notificationService.showNotification({
      dismissible: true,
      ttl: this.notificationDismissTimeout * 1000,
      modifier: success ? 'success' : 'error',
      ...notification,
    });
  }

  private responseHandler(
    response: StopCheckPostResponse,
    requestInfo?: StopCheckRequestInfo
  ) {
    if (response.stopCheck?.status && requestInfo) {
      this.showNotification(true, {
        header: this.successNotificationHeader,
        message: this.successNotificationMessage,
      });
      this.markStopRequestAsSubmitted(requestInfo);
    } else {
      this.hasErrorOnSubmit$.next(true);
      this.showNotification(false, {
        header: this.failureNotificationHeader,
        message: this.failureNotificationMessage,
      });
    }
  }

  discardStopRequest() {
    this.stateService.initStopCheckRequest();
  }

  submitStopRequest(requestInfo: StopCheckRequestInfo) {
    this.apiHttpService
      .postStopCheckRequest(requestInfo)
      .pipe(take(1))
      .subscribe(
        (res) => this.responseHandler(res, requestInfo),
        (err) => this.responseHandler(err)
      );
  }

  markStopRequestAsSubmitted(requestInfo: StopCheckRequestInfo) {
    this.isStopRequestSubmitted$.next(true);
    this.stateService.markStopRequestAsSubmitted(requestInfo);
  }

  editStopRequest(requestInfo: StopCheckRequestInfo) {
    this.stateService.sendStopRequestToEdit(requestInfo);
  }

  ngOnDestroy() {
    this.gc$.next();
  }
}
