import { Injectable } from '@angular/core';
import { ProductSummaryHttpService } from '@backbase/data-ang/arrangements';
import { StopCheckPostResponse } from '@backbase/data-ang/stop-checks';
import { ArrangementList, ChecksService } from '@backbase/payment-orders-ang';
import { Observable, of } from 'rxjs';
import { map, shareReplay, switchMap, withLatestFrom } from 'rxjs/operators';

import {
  arrangementListFromService,
  requestInfoToPostRequestBody,
} from '../mappers';
import { arrangementsPageSize } from '../models/data';
import { StopCheckRequestInfo } from '../models/types';
import { StopChecksPreferencesService } from './stop-checks-preferences.service';

@Injectable()
export class ApiHttpService {
  private readonly debitAccountsParameters$ = of({
    debitAccount: true,
    size: arrangementsPageSize,
  }).pipe(
    withLatestFrom(
      this.stopChecksPreferencesService.resourceName$,
      this.stopChecksPreferencesService.businessFunction$,
      this.stopChecksPreferencesService.privilege$
    ),
    map(([params, resourceName, businessFunction, privilege]) => ({
      resourceName,
      businessFunction,
      privilege,
      ...params,
    }))
  );

  public debitAccounts$: Observable<ArrangementList> =
    this.debitAccountsParameters$.pipe(
      withLatestFrom(this.debitAccountsParameters$, (_, params) => params),
      switchMap((params) =>
        this.productSummaryData.getArrangementsByBusinessFunction(params)
      ),
      map((response) => arrangementListFromService(response)),
      shareReplay(1)
    );

  constructor(
    private readonly productSummaryData: ProductSummaryHttpService,
    private readonly stopCheckService: ChecksService,
    private readonly stopChecksPreferencesService: StopChecksPreferencesService
  ) {}

  postStopCheckRequest(
    payload: StopCheckRequestInfo
  ): Observable<StopCheckPostResponse> {
    return this.stopCheckService.postStopChecks(
      requestInfoToPostRequestBody(payload)
    );
  }
}
