import {
  ChangeDetectionStrategy,
  Component,
  Directive,
  EventEmitter,
  Input,
  OnInit,
  Output,
} from '@angular/core';
import {
  AbstractControl,
  FormGroup,
  ValidatorFn,
  Validators,
} from '@angular/forms';
import { BbTemplate, DeprecationsService } from '@backbase/foundation-ang/core';
import { InternalAccountFormItem } from '@backbase/payment-orders-ang';
import { Observable } from 'rxjs';
import { StopCheckRequestType } from '../../models/data';
import {
  getCheckRangeError,
  getLastCheckLessThanFirstError,
} from '../../validators';

@Component({
  selector: 'bb-stop-check-form',
  templateUrl: './stop-check-form.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class StopCheckFormComponent implements OnInit {
  private static _id = 0;
  private _showValidation = false;

  readonly id = StopCheckFormComponent._id++;
  readonly hostRef: StopCheckFormComponent = this;

  get payFromControl(): AbstractControl | null {
    return this.relativeFormGroup.get('payFromAccount');
  }

  selectedAccount!: InternalAccountFormItem;
  accountFormat = {
    length: 0,
    maskRange: [0, 0],
    segments: 0,
  };

  /**
   * @deprecated since version 3.9.3
   * Validation errors in form appear on blur.
   */
  @Input()
  set showValidation(val: boolean) {
    this.deprecationService.logDeprecatedFeature(
      `StopChecksFormComponent: showValidation input is deprecated since version 3.9.3,
      validation errors in form appear on blur`
    );
    this._showValidation = val;
  }

  get showValidation(): boolean {
    return this._showValidation;
  }

  /**
   * Form group that contains relative form controls
   */
  @Input() relativeFormGroup!: FormGroup;

  /**
   * Form group that contains relative form controls
   */
  @Input() payFromAccounts$!: Observable<InternalAccountFormItem[]>;

  /**
   * Event will be triggered on form submit
   */
  @Output() formSubmit = new EventEmitter();

  /**
   * Value of currencies property in widget preferences
   */
  @Input() currencies!: string[];

  /**
   * Custom range number to identify maximum consecutive check number range.
   * Default value `0`
   */
  @Input() checksRangeNumber = 0;

  /**
   * Event will be triggered on payment cancel action
   */
  @Output() cancelForm = new EventEmitter();

  private crossFormValidation(rangeNumber: number): ValidatorFn {
    return ((group: FormGroup): void => {
      const { controls, value } = group;
      const checkNum = controls.checkNumber;
      const firstCheckNum = controls.firstCheckNumber;
      const lastCheckNum = controls.lastCheckNumber;
      const checkNumValidators = [
        Validators.required,
        Validators.pattern(/^\d+$/),
      ];

      if (value['stopRangeType'] === StopCheckRequestType.SINGLE) {
        firstCheckNum.clearValidators();
        firstCheckNum.setErrors(null);
        lastCheckNum.clearValidators();
        lastCheckNum.setErrors(null);
        checkNum.setValidators(checkNumValidators);
      } else {
        checkNum.clearValidators();
        checkNum.setErrors(null);
        firstCheckNum.setValidators(checkNumValidators);
        lastCheckNum.setValidators(checkNumValidators);
        lastCheckNum.setErrors(
          getLastCheckLessThanFirstError(firstCheckNum, lastCheckNum)
        );
        lastCheckNum.setErrors(
          getCheckRangeError(firstCheckNum, lastCheckNum, rangeNumber)
        );
      }
    }) as ValidatorFn;
  }

  constructor(private readonly deprecationService: DeprecationsService) {}

  ngOnInit() {
    // populate value to show it in edit mode
    if (this.relativeFormGroup && this.payFromControl?.value['id']) {
      const formValue = this.payFromControl?.value;
      // account selector component expect account number attribute name as 'number' which is not available in edit flow
      this.selectPayFromAccount({
        ...formValue,
        number: formValue.accountNumber,
      });
    }

    this.relativeFormGroup?.setValidators(
      this.crossFormValidation(this.checksRangeNumber)
    );
    this.relativeFormGroup.updateValueAndValidity();
  }

  onCancelForm() {
    this.cancelForm.emit();
  }

  onBlurAccount() {
    if (this.selectedAccount) {
      return;
    }
    this.payFromControl?.setErrors({ required: true });
    this.payFromControl?.markAsTouched();
    this.payFromControl?.markAsDirty();
  }

  selectPayFromAccount(account: InternalAccountFormItem): void {
    if (this.payFromControl && account.id) {
      this.payFromControl.setValue({
        id: account.id,
        name: account.name,
        accountType: account.accountType,
        accountNumber: account.accountNumber,
        currency: account.currency,
        balance: account.balance,
      });

      this.selectedAccount = account;
      this.payFromControl?.markAsTouched();
      this.payFromControl?.markAsDirty();
    }
  }

  onSubmit() {
    const { controls } = this.relativeFormGroup;
    for (const control in controls) {
      if (controls.hasOwnProperty(control)) {
        controls[control].markAsTouched();
        controls[control].markAsDirty();
      }
    }
    this.formSubmit.emit();
  }

  getIdByControl(controlName: string): string {
    return `${controlName}StopChecksError${this.id}`;
  }

  getDescribedby(controlName: string): string | null {
    const control = this.relativeFormGroup?.get(controlName);
    return control?.invalid && control?.touched
      ? this.getIdByControl(controlName)
      : null;
  }

  getDescribedbyBySubmit(): string | null {
    return (
      Object.keys(this.relativeFormGroup.controls)
        .reduce((acc, controlName) => {
          const errorId = this.getDescribedby(controlName);
          acc += errorId ? `${errorId} ` : '';
          return acc;
        }, '')
        .trim() || null
    );
  }
}

@Directive({
  selector: '[bbStopCheckFormComponentCustomizable]',
})
export class StopCheckFormComponentCustomizableDirective extends BbTemplate<
  StopCheckFormComponent,
  undefined
> {}
