import {
  ChangeDetectionStrategy,
  Component,
  Directive,
  EventEmitter,
  Input,
  Output,
} from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { Observable } from 'rxjs';

import { InitialState } from '../../models/types';

@Component({
  selector: 'bb-stop-check-review',
  templateUrl: './stop-check-review.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class StopCheckReviewComponent {
  readonly hostRef: StopCheckReviewComponent = this;
  showConfirmation = false;
  disableSubmitAction = false;
  showAlert = false;

  /**
   * State of submitted stop check request
   */
  @Input() requestIsSubmitted!: Observable<boolean>;

  /**
   * Valid/Identified stop check request
   */
  @Input() request!: InitialState['requestInfo'];

  /**
   * State of submitted payment
   */
  @Input() stopRequestIsSubmitted!: boolean;

  /**
   * Event will be triggered on payment reset action
   */
  @Output() triggerStopRequestReset = new EventEmitter<void>();

  /**
   * Event will be triggered on payment submit action
   */
  @Output() triggerStopRequestSubmit = new EventEmitter<void>();

  /**
   * Event will be triggered on payment edit action
   */
  @Output() triggerStopRequestEdit = new EventEmitter<void>();

  /**
   * Flag for failing request submit
   */
  @Input() set failedToSubmit(v: boolean) {
    this.showAlert = v;
  }

  submitStopRequest() {
    this.disableSubmitAction = true;
    this.triggerStopRequestSubmit.emit();
  }

  resetStopRequest() {
    this.triggerStopRequestReset.emit();
  }

  editStopRequest() {
    this.triggerStopRequestEdit.emit();
  }

  toggleConfirmationDialog() {
    this.showConfirmation = !this.showConfirmation;
  }

  hideAlert() {
    this.showAlert = false;
  }
}

@Directive({
  selector: '[bbStopCheckReviewComponentCustomizable]',
})
export class StopCheckReviewComponentCustomizableDirective extends BbTemplate<
  StopCheckReviewComponent,
  undefined
> {}
